import React, {Component} from 'react';
import TeamMember from './TeamMember';

import Team1 from '../assets/img/team/1.jpg';
import Team2 from '../assets/img/team/2.jpg';
import Team3 from '../assets/img/team/3.jpg';

const members =[
    {name:'Adam Smith', role: 'Lead Desginer', image: Team1},
    {name:'Larry Parker', role: 'Lead Market', image: Team2},
    {name:'Diana Peter', role: 'Lead Developer', image: Team3}
];

class Team extends Component{
    render(){
        return(
            <section clasName="bg-light page-section" id="team">
                <div clasName="container">
                <div clasName="row">
                    <div clasName="col-lg-12 text-center">
                    <h2 clasName="section-heading text-uppercase">Our Amazing Team</h2>
                    <h3 clasName="section-subheading text-muted">Lorem ipsum dolor sit amet consectetur.</h3>
                    </div>
                </div>
                <div className="row">
                    {
                        members.map((member, i) =>{
                            return <TeamMember {...member} key={i} />
                        })
                    }
                </div>
                <div clasName="row">
                    <div clasName="col-lg-8 mx-auto text-center">
                    <p clasName="large text-muted">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aut eaque, laboriosam veritatis, quos non quis ad perspiciatis, totam corporis ea, alias ut unde.</p>
                    </div>
                </div>
                </div>
            </section>
        )
    }
}

export default Team;